.videoframe {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
}

.paper {
    margin: 10px;
    position: fixed;
    width: 300px;
    height: 220px;
    bottom: 0px;
}

.controls {
    height: 0px !important;
    overflow: hidden;
    margin-top: 175px !important;
    transition: all 0.3s !important;
}

.paper:hover .controls {
    height: 40px !important;
    transition: all 0.3s !important;
}


