.myCodepannel {
    .topbarcontainer {
        background: #2f3129;
        color: white;
        height: 37px;
        width: 100%;
        overflow-x: auto;
        box-shadow: inset 0px 0px 7px -3px #c1c1c1;

        &::-webkit-scrollbar {
            width: 0px;
            height: 3px;
          }
          
          /* Track */
          &::-webkit-scrollbar-track {
            background: #f1f1f1;
          }
          
          /* Handle */
          &::-webkit-scrollbar-thumb {
            background: #888;
          }
          
          /* Handle on hover */
          &::-webkit-scrollbar-thumb:hover {
            background: #555;
          }
    }
  


    .tabs {
        padding: 8px 12px;
        cursor: pointer;

        .closeIcon:hover {
            color: #ff305c !important;
        }
        &:hover {
            background-color: #4b4b4b !important;
        }
    }

    .tabs.active{
        background-color: #202020 !important;
    }


    .ace-monokai .ace_print-margin {
        width: 0px !important;
        display: none !important;
    }


    .ace_content {
        // position: relative !important;
        // left: 0px !important;
    }

}