
.closeButton{
    float:right;
    cursor:pointer;
}

.createProjectCard{
    transition: all 1s !important;
    width: 0px;
    height: 0px;
}

.ShowCreateProjectCard{
    transition: all 1s !important;
    width: 550px;
    height: 200px;
}

.layerDivbefore{
    transition: all 1s !important;
    width: 0px;
    height: 0px;
    position: absolute;
    top: 477px;
    left: 204px;
    justify-content: center;
    align-items: center;
    display: flex;
}
.layerDiv{
    transition: all 1s !important;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    // background-color: rgba(167, 167, 167, 0);
    justify-content: center;
    align-items: center;
    display: flex;
}

.ProjectCard{
    cursor: pointer;
    transition: all 0.3s !important;
    box-shadow: 0px -3px 0px 0px #7a95fa, 0px 0px 5px 0px grey !important;

    &:hover{
        box-shadow: 0px 0px 0px 3px #7a95fa !important;
    }
}
