.homeBg{
    height: 100%;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.1) 1px, transparent 1px) 0% 0% / 40px 40px, linear-gradient(rgba(0, 0, 0, 0.1) 1px, transparent 1px) rgb(54, 54, 69);
    overflow: hidden;
    padding:20px
}

.registerCard{
    padding: 35px 35px 100px 35px;
    transform: rotate3d(2, 2, 1, -45deg);
    transition: all 0.5s !important;
    border: 1px solid #ea7577;
    box-shadow: 0px 0px 8px #ea7577 !important;
}
.registerCard:hover{
    transform: rotate3d(1, 1, 1, 0deg);
}
.loginCard{
    margin-top: -75px;
    padding: 35px 35px 35px 35px;
    transform: rotate3d(2, 2, 1,  -45deg);
    transition: all 0.5s !important;
    border: 1px solid #53a3f4;
    box-shadow: 0px 0px 8px #4d97f3 !important;
}
.loginCard:hover{
    transform: rotate3d(1, 1, 1, 0deg);
}